import React from "react";
import { CustomColumns, Feature, Trigger } from "../../../types";
import { useQuery } from "react-query3";
import { Button, PageHeader, Space } from "antd";
import { Modal } from "../Modal";
import { setTriggerEventName } from "components/utils/utils";
import useAppStore from "components/context/app/Hooks";
import { LinkOutlined } from "@ant-design/icons";

const TagLink = React.lazy(() => import("../TagLink"));
const Table = React.lazy(() => import("../Table"));
const EditModal = React.lazy(() => import("components/ui/EditModal"));
const DeleteButton = React.lazy(() => import("components/ui/DeleteButton"));
const NotLicensed = React.lazy(() => import("../../pages/notLicensed"));
const NewTriggerForm = React.lazy(() => import("./triggerModal"));

export default function Triggers() {
  const { licensed } = useAppStore();
  const { data: triggers, isLoading, refetch } = useQuery<Trigger[]>(
    "/trigger",
    {
      enabled: false,
      refetchOnWindowFocus: false,
    }
  );

  const columns: CustomColumns<Trigger>[] = [
    {
      key: "name",
      dataIndex: "name",
      title: "Name",
    },
    {
      key: "triggerScript",
      dataIndex: "triggerScript",
      title: "Trigger",
      render: (text, record) => (
        <TagLink to={`/admin/automation/scripts/${record?.triggerScript}`} text={record?.triggerScript} />
      ),
    },
    {
      key: "eventType",
      dataIndex: "eventType",
      title: "Event",
      render: (value) => setTriggerEventName(value),
    },
    {
      key: "triggerType",
      title: "Type",
      render: (value, { dashboard, script }) => {
        return dashboard === null && script === null ? "Global" : "Resource";
      },
    },
    {
      key: "environment",
      dataIndex: "environment",
      title: "Environment",
    },
    {
      key: "script",
      dataIndex: "script",
      title: "Script",
      render: (text, record) =>
        record?.script && <TagLink to={`/admin/automation/scripts/${record?.script}`} text={record?.script} />,
    },
    {
      key: "dashboard",
      dataIndex: "dashboard",
      title: "Dashboard",
      editable: true,
      render: (text, record) =>
        record?.dashboard && (
          <TagLink to={`/admin/dashboards/${record?.dashboard}`} text={record?.dashboard} />
        ),
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      editable: false,
      render: (text, record) => (
        <Space>
          <Modal>
            <EditModal record={record} allowedWithOneWayGitSync={false} />
          </Modal>
          <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
        </Space>
      ),
    },
  ];

  return licensed(Feature.Automation) ? (
    <PageHeader title="Triggers" subTitle="Trigger scripts when certain events happen within Universal." extra={[
      <Button icon={<LinkOutlined />} href="https://docs.powershelluniversal.com/automation/triggers" target="_blank">Documentation</Button>
    ]}>
      <Table
        title="Triggers"
        actions={[
          <Modal>
            <NewTriggerForm />
          </Modal>,
        ]}
        columns={columns}
        data={triggers}
        loading={isLoading}
        queryFn={refetch}
      />
    </PageHeader>
  ) : (
    <NotLicensed title="Triggers are only available for licensed instances." />
  );
}
